/* make variables */
:root {
  --lightgray: #d9d9d9;
  --gray: #9c9c9c;
  --firstColor: #09ff00;
  --cyan: #1db891;
  --darkGrey: #464d53;
  --caloryCard: #656565;
  --menuColor: #33383b;
  --menuHoverColor: #282b2c;
  --planCard: linear-gradient(
    180deg,
    var(--cyan) 1.14%,
    var(--firstColor) 100.75%
  );
  --appColor: #3c3f45;
}
.App {
  font-family: Arial, Helvetica, sans-serif;
  background-image: url(data:image/png;base64,);
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar {
  display: none;
}
.text-stroke {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--firstColor);
}
.text-stroke-white {
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--lightgray);
}
.blur {
  background: #09ff00ad;
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: 0;
}

@media screen and (max-width: 1350px) {
  html {
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 1060px) {
  html {
    font-size: 0.7rem;
  }
}
@media screen and (min-width: 1600px) {
  html {
    font-size: 1.2rem;
  }
}
@media screen and (min-width: 2500px) {
  html {
    font-size: 1.6rem;
  }
}
