.header {
  display: flex;
  justify-content: space-between;
  position: absolute;
  width: 70%;
  padding: 1rem 2rem;
  z-index: 1;
}
.closeButton {
  display: none;
}
.floating-button {
  position: fixed;
  z-index: 2;
  right: -1rem;
  top: 0.5rem;
  cursor: pointer;
  border: none;
  background: transparent;
}
.floating-button > img {
  transform: rotate(180deg);
  width: 8rem;
}
.header span {
  font-family: "Zen Dots", cursive;
  color: var(--lightgray);
  position: relative;
  border-bottom: 3px solid var(--lightgray);
  left: 1rem;
  top: 0.7rem;
  font-size: 1.3rem;
  text-transform: uppercase;
  height: 1.8rem;
  width: 14rem;
}
.header span::after {
  content: "";
  position: absolute;
  left: 0rem;
  top: 1.8rem;
  width: 180px;
  border-bottom: 3px solid var(--firstColor);
  animation: moveBar 5s linear infinite;
}
@keyframes moveBar {
  0% {
    width: 0rem;
  }
  50% {
    width: 14rem;
  }
  100% {
    width: 0rem;
  }
}
.header-menu {
  list-style: none;
  display: flex;
  gap: 2rem;
  color: white;
}

.header-menu > li:hover {
  color: var(--firstColor);
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .navMobileDiv {
    display: flex;
    flex-direction: column;
    align-items: end;
  }
  .barsButton {
    position: relative;
    right: 1rem;
    width: 3rem;
    height: 2rem;
    background-color: transparent;
    padding: 0.8rem;
    border-radius: 5px;
  }
  .closeButton {
    display: block;
    position: relative;
    right: 1rem;
    width: 3rem;
    height: 2rem;
    background-color: transparent;
    padding: 0.8rem;
    border-radius: 5px;
  }
  .barsButton:hover,
  .closeButton:hover {
    fill: var(--firstColor);
    background-color: var(--menuHoverColor);
    cursor: pointer;
  }
  .floating-button {
    display: none;
  }
  .header span {
    top: 2rem;
    font-size: 1.7rem;
    width: 18rem;
  }
  @keyframes moveBar {
    0% {
      width: 0rem;
    }
    50% {
      width: 18rem;
    }
    100% {
      width: 0rem;
    }
  }
  .header > :nth-child(3) {
    position: fixed;
    right: 0rem;
    top: 2.3rem;
    z-index: 99;
  }
  .header-menu {
    width: 100vw;
    padding: 2rem 0rem;
    flex-direction: column;
    background-color: var(--menuColor);
    font-size: 1.8rem;
    border-radius: 0.5rem;
  }
  .header-menu li a {
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .header-menu li:hover {
    background: var(--menuHoverColor);
  }
}
